.container {
  display: grid;
  grid-template: "a b" auto "c d" auto "e f" auto/30px auto;
  place-items: center;
}
.container > * {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container > *:nth-child(1) {
  grid-area: a;
}
.container > *:nth-child(2) {
  grid-area: b;
}
.container > *:nth-child(3) {
  grid-area: c;
}
.container > *:nth-child(4) {
  grid-area: d;
}
.container > *:nth-child(5) {
  grid-area: e;
}
.container > *:nth-child(6) {
  grid-area: f;
}