.container {
  display: inline-block;
  padding: 6px;
  transition: transform 50ms ease-in-out;
  color: rgba(255, 255, 255, 0.7);
  border: none;
  background: none;
}
.container:hover {
  color: white;
  cursor: pointer;
}
.container:active:not(:disabled) {
  transform: scale(0.95);
}
.container:disabled {
  color: rgba(255, 255, 255, 0.4);
  cursor: default;
}