.main {
  height: 100vh;
}
.main-enter {
  opacity: 0;
}
.main-enter-active, .main-enter-done {
  opacity: 1;
}
.main-enter-active {
  transition: opacity 200ms ease-in-out;
}
.main-exit {
  opacity: 1;
}
.main-exit-active, .main-exit-done {
  opacity: 0;
}
.main-exit-active {
  transition: opacity 200ms ease-in-out;
}
.main .top {
  position: fixed;
  top: 0;
  left: 0;
  padding: 4px 4px 0;
}
.main .center {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.main .bottom {
  position: fixed;
  inset: auto 0 0;
  padding: 0 4px 4px;
}
.main .dimming {
  position: fixed;
  user-select: none;
  z-index: -1;
  transition: opacity 200ms ease-in-out;
}
.main .dimming .top {
  position: fixed;
  inset: 0 0 auto;
  height: 60px;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.15) 0%, transparent 100%);
}
.main .dimming .bottom {
  position: fixed;
  inset: auto 0 0;
  height: 120px;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.15) 0%, transparent 100%);
}